<template>
    <div class="pay">
        <div class="content">
            <p class="top">请在 <span class="time">10:10:10</span> 内完成支付</p>
            <p class="middle">需支付</p>
            <p class="bottom">￥ 100.00</p>
        </div>
        <div class="way">
            <div class="title"> <span class="block"></span> 请选择支付方式</div>
            <ul class="list">
                <li class="item">
                    <div class="logo">
                        <img src="" alt="">
                    </div>
                    <div class="info">
                        <p class="source">微信支付</p>
                        <p class="desc">推荐微信用户使用</p>
                    </div>
                    <div class="check">
                        <cube-checkbox v-model="weixin" label=""></cube-checkbox>
                    </div>
                </li>
                <li class="item">
                    <div class="logo">
                        <img src="" alt="">
                    </div>
                    <div class="info">
                        <p class="source">余额支付</p>
                        <p class="desc">余额 ￥ 100.00</p>
                    </div>
                    <div class="check">
                        <cube-checkbox v-model="yue" label=""></cube-checkbox>
                    </div>
                </li>
            </ul>
            <div class="recharge">
                <span class="text">余额充值享受有优惠</span>
                <span class="btn" @click="_recharge">去充值</span>
            </div>
        </div>
        <div class="btn-pay">
            支付
        </div>
    </div>
</template>
<script>
export default {
    name: 'pay',
    data() {
        return {
            weixin:  false,
            yue: false,
        };
    },
    methods: {
        _recharge() {
            this.$router.push({ path: '/recharge' });
        },
    }
}
</script>

<style lang="stylus" scoped>
.pay {
    font-family: HiraginoSansGB-W3;
    overflow: auto;
    height: 100%;
    .content {
        background-color: #ffffff;
        padding: 60px 0;
        text-align: center;
        .top {
            font-size: 28px;
            color: #000000;
            .time {
                color: #F43030;    
            }
        }
        .middle {
            margin-top 40px;
            font-size: 28px;
            color: #000000; 
        }
        .bottom {
            margin-top 40px;
            font-size: 32px;
            color: #FF8C45;    
        }
    }
    .way {
        margin-top: 20px;
        background-color: #ffffff;
        .title {
            line-height: 80px;
            font-size: 28px;
            font-weight: bold;
            color: #333333;
            padding: 0 30px;
            .block {
                display: inline-block;
                margin-right: 20px;
                width: 8px;
                height: 24px;
                background: #FF8C45;
                border-radius:4px;    
            }
        }
        .list {
            padding: 0 30px;
            .item {
                padding: 20px 0;
                border-bottom: 0.5px solid #F2F2F2;
                display: flex;
                justify-content: space-between;
                .logo {
                    width: 80px;
                    height: 80px;
                    border: 0.5px solid #F2F2F2;  
                }
                .info {
                    flex: auto;
                    margin-left: 20px;
                    .source {
                        color: #333333;
                        font-size: 32px;
                    }
                    .desc {
                        margin-top: 20px;
                        color: #666666;
                        font-size: 28px;
                    }
                }
            }    
        }
        .recharge {
            color: #ffffff;
            text-align: center;
            background-color: #FFEFE5;
            padding: 16px 0;
            .text {
                font-size: 24px;
                color: #FF7928;
                padding-right: 28px;
            }
            .btn {
                display: inline-block;
                width: 120px;
                height: 48px;
                font-size: 20px;
                line-height: 48px;
                background: #FF8C45;
                border-radius: 4px;    
            }
        }
    }
    .btn-pay {
        position: fixed;
        bottom: 82px;
        left: 75px;
        width: 600px;
        height: 98px;
        line-height: 98px;
        text-align: center;
        background: #4A4C59;
        color: #ffffff;
        border-radius: 10px;
    }
}
</style>




